/**
 * Extend an object with another.
 *
 * @param {Object, ...} src, ...
 * @return {Object} merged
 * @api private
 */
module.exports = function (src) {
  var objs = [].slice.call(arguments, 1),
      obj;

  for (var i = 0, len = objs.length; i < len; i++) {
    obj = objs[i];

    for (var prop in obj) {
      src[prop] = obj[prop];
    }
  }

  return src;
};